import React, { useEffect } from 'react';
import { Route, useHistory } from "react-router";
import clsx from 'clsx';
import { useTheme } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import CssBaseline from '@material-ui/core/CssBaseline';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import AccountCircle from '@material-ui/icons/AccountCircleRounded';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import WbSunny from '@material-ui/icons/WbSunnyRounded';
import Brightness2Icon from '@material-ui/icons/Brightness2Rounded';
import MenuIcon from '@material-ui/icons/Menu';
import HomeIcon from '@material-ui/icons/Home';
import PostAddIcon from '@material-ui/icons/PostAdd';
import ChatIcon from '@material-ui/icons/Chat';
import useStyles from '../styles/pages/main';
import { useDispatch, useSelector } from "react-redux";
import { switchTheme } from '../redux/actions';
import HomePage from './home';
import { title } from '../application.json';

const MainPage = () => {
  const classes = useStyles();
  const theme = useTheme();
  const dispatch = useDispatch();
  const [open, setOpen] = React.useState(false);

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  const history = useHistory();

  const logout = () => {
    localStorage.setItem("token", "");
    history.push("/signin");
  };

  useEffect(() => {
    if (!localStorage.getItem("token")) {
      history.push("/signin");
    }
  }, [history]);

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar
        position="fixed"
        className={clsx(classes.appBar, {
          [classes.appBarShift]: open,
        })}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            className={clsx(classes.menuButton, {
              [classes.hide]: open,
            })}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h4" className={classes.title}>
            { title }
          </Typography>
          <IconButton
              aria-label="account of current user"
              aria-controls="primary-search-account-menu"
              aria-haspopup="true"
              color="inherit"
              onClick={() => dispatch(switchTheme())}
          >
            {
              useSelector(store => store.theme)
                ? <Brightness2Icon />
                : <WbSunny />
            }
          </IconButton>
          <IconButton
              aria-label="account of current user"
              aria-controls="primary-search-account-menu"
              aria-haspopup="true"
              color="inherit"
              onClick={logout}
          >
            <ExitToAppIcon />
          </IconButton>
        </Toolbar>
      </AppBar>
      <Drawer
        variant="permanent"
        className={clsx(classes.drawer, {
          [classes.drawerOpen]: open,
          [classes.drawerClose]: !open,
        })}
        classes={{
          paper: clsx({
            [classes.drawerOpen]: open,
            [classes.drawerClose]: !open,
          }),
        }}
      >
        <div className={classes.toolbar}>
          <IconButton onClick={handleDrawerClose}>
            {theme.direction === 'rtl' ? <ChevronRightIcon /> : <ChevronLeftIcon />}
          </IconButton>
        </div>
        <Divider />
        <List>
          {[
            {page: 'Home', link: "/home", component: <HomeIcon />},
            {page: 'Profile', link: "/profile", component: <AccountCircle />},
            {page: 'Post', link: "/post", component: <PostAddIcon />},
            {page: 'Messages', link: "/message", component: <ChatIcon />}
          ].map((entry, index) => (
            <ListItem onClick={() => history.push(entry.link)} key={index} className={classes.links}>
              <ListItemIcon>{entry.component}</ListItemIcon>
              <ListItemText primary={entry.page} />
            </ListItem>
          ))}
        </List>
      </Drawer>
      <main className={classes.content}>
        <div className={classes.toolbar} />
        <Route path="/home">
          <HomePage />
        </Route>
        <Route path="/profile">
          <h1>Profile</h1>
        </Route>
        <Route path="/post">
          <h1>Post</h1>
        </Route>
        <Route path="/message">
          <h1>Message</h1>
        </Route>
      </main>
    </div>
  );
}

export default MainPage;
